@import url(https://fonts.googleapis.com/css?family=Coming+Soon);
body {
  margin: 0;
  padding: 0;
  font-family: "Coming Soon", cursive;
}

.App {
  text-align: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/intermediary/f/61f7685f-47e5-49a0-b5cb-ca6b8cab4228/drok89-a7a2a550-ba87-40a6-9701-b635550d55b3.jpg");
  background-position: center center;
}
.Hangman {
  width: 400px;
  color: black;
  text-align: center;
  zoom: 1.3;
}

.Hangman img {
  width: 15.65rem; /* 250 / 16 */
  height: 17.5rem; /* 280 / 16 */
  border: 10px solid white;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
}

.Hangman h1 {
  font-size: 4rem;
  font-weight: 100;
  margin-bottom: 0.1em;
  margin-top: 0;
}

.Hangman-nWrong {
  font-weight: 600;
}

.Hangman-word {
  letter-spacing: 1em;
  margin: 0.4em -1em 0.2em 0;
  font-size: 2rem;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 250px;
  margin-top: 0;
}

.Hangman button {
  border: none;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  margin: 5px 2px 2px 2px; /* top | right | bottom | left */
  letter-spacing: 1px;
  outline: none;
  background: #00bcd4; /* before → #ffc107*/
  color: #fff;
  box-shadow: 0 6px #0097a7; /*  before → #ff9800; offset-x | offset-y | color */
  border-radius: 5px;
  padding-top: 4px;
}

.Hangman button:hover {
  background-color: #0097a7; /* before → #ff9800 */
}

.Hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  box-shadow: 0 6px #9e9e9e;
}

#reset {
  width: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background: #9c27b0;
  box-shadow: 0 6px #7b1fa2;
}

#reset:hover {
  background: #7b1fa2;
}
